import { __decorate } from "tslib";
import { Vue, Component, Mixins, Watch } from 'vue-property-decorator';
import { restartMapper } from '@/modules/restart/module/store';
import ListRestart from '@/modules/restart/components/ListRestart.vue';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
import MsgBoxTools from '@/modules/restart/mixins/MsgBoxTools';
import MessageBox from '@/components/MessageBox.vue';
import animateIfVisible from '@/mixins/animateIfVisible';
const Mappers = Vue.extend({
    computed: {
        ...restartMapper.mapState(['list']),
        ...restartMapper.mapGetters(['isLoading'])
    },
    methods: {
        ...restartMapper.mapActions(['getList']),
    }
});
let ModuleRestart = class ModuleRestart extends Mixins(Mappers, MsgBoxToolsApp, MsgBoxTools) {
    constructor() {
        super(...arguments);
        this.secondBtn = { type: 'success', isPlain: true };
    }
    get fetchListFailed() { return this.requestStatus === 'failFetchList'; }
    async onListChange(val) {
        if (val.data && val.data.length) {
            await this.$nextTick();
            animateIfVisible();
        }
    }
    created() {
        this.updateList();
    }
    goToPageApp() { this.$router.push({ path: '/' }); }
    onFirstBtnClick() {
        if (this.msgBoxIsShown)
            this.closeMsgBox();
        switch (this.requestStatus) {
            case 'failFetchList':
                this.updateList();
                break;
        }
    }
    updateList() {
        if (this.list.isLoading)
            return;
        this.getList()
            .catch((err) => {
            if (err && err.status && err.status.toString().slice(0, 2) == 40)
                this.$emit('goToPageAuth');
            else {
                this.requestStatus = 'failFetchList';
                this.openMsgBox();
            }
        });
    }
};
__decorate([
    Watch('list', { deep: true })
], ModuleRestart.prototype, "onListChange", null);
ModuleRestart = __decorate([
    Component({
        components: {
            ListRestart,
            MessageBox
        }
    })
], ModuleRestart);
export default ModuleRestart;
